/* mission.css */

/* Layout container */
.mission-container {
    max-width: 1100px;
    margin: 0 auto;
    padding: 2rem;
    font-family: 'Inter', sans-serif; /* or your preferred font */
    color: #333;
  }
  
  /* Hero / Intro */
  .mission-hero {
    text-align: center;
    margin-bottom: 3rem;
  }
  
  .mission-hero h1 {
    font-size: 2.2rem;
    color: #205EA3;
    margin-bottom: 1rem;
  }
  
  .mission-hero p {
    font-size: 1.1rem;
    line-height: 1.6;
    max-width: 700px;
    margin: 0 auto;
  }
  
  /* Main content section */
  .mission-content {
    margin-bottom: 3rem;
  }
  
  .mission-text-block {
    margin-bottom: 2rem;
  }
  
  .mission-text-block h2 {
    font-size: 1.5rem;
    color: #205EA3;
    margin-bottom: 0.75rem;
  }
  
  .mission-text-block p {
    font-size: 1rem;
    line-height: 1.6;
    margin-bottom: 1rem;
  }
  
  /* Paper snippet styling */
  .snippet-block {
    background: #fafafa;
    border-left: 4px solid #205EA3;
    padding: 1rem 1.5rem;
    margin-left: 0;
    margin-right: 0;
  }
  
  .paper-quote {
    margin-top: 1rem;
    font-style: italic;
    color: #555;
  }
  
  /* Call to Action */
  .mission-cta {
    text-align: center;
    background: #f7f7f7;
    padding: 2rem;
    border-radius: 4px;
  }
  
  .mission-cta h2 {
    font-size: 1.8rem;
    margin-bottom: 1rem;
    color: #205EA3;
  }
  
  .mission-cta p {
    font-size: 1rem;
    line-height: 1.5;
    max-width: 600px;
    margin: 0.5rem auto 2rem;
    color: #555;
  }
  
  /* CTA Button */
  .cta-button {
    display: inline-block;
    background: #205EA3;
    color: #fff;
    text-decoration: none;
    font-weight: 600;
    font-size: 1rem;
    padding: 0.8rem 1.4rem;
    border-radius: 4px;
    transition: background 0.3s ease;
  }
  
  .cta-button:hover {
    background: #003366;
  }
  
  /* Basic Responsive Tweaks */
  @media (max-width: 768px) {
    .mission-hero h1 {
      font-size: 1.8rem;
    }
    .mission-hero p,
    .mission-text-block p,
    .mission-cta p {
      font-size: 0.95rem;
    }
  }
  